import React, { useState, useEffect } from 'react'
import * as fcl from '@onflow/fcl'
import './App.css'
import WalletConnect from './components/WalletConnect'
import PriceViewer from './components/PriceViewer'
import NodeManagement from './components/NodeManagement'
import PriceHistory from './components/PriceHistory'

fcl.config()
  .put('accessNode.api', 'https://rest-testnet.onflow.org')
  .put('discovery.wallet', 'https://fcl-discovery.onflow.org/testnet/authn')

const ORACLE_ADDRESS = '0x791e03a1b0a978ff'

export default function App() {
  const [user, setUser] = useState(null)
  const [prices, setPrices] = useState({})
  const [oracleStats, setOracleStats] = useState({ quorumSize: 0, nodeCount: 0 })
  const [activeTab, setActiveTab] = useState('prices')

  useEffect(() => {
    fcl.currentUser.subscribe(setUser)
  }, [])

  useEffect(() => {
    fetchPrices()
    fetchOracleStats()
    const interval = setInterval(() => {
      fetchPrices()
      fetchOracleStats()
    }, 15000)
    return () => clearInterval(interval)
  }, [])

  const fetchPrices = async () => {
    try {
      const response = await fcl.query({
        cadence: `
          import FlowOracle from ${ORACLE_ADDRESS}
          
          pub fun main(): {String: UFix64} {
            return FlowOracle.getAllPrices()
          }
        `
      })
      setPrices(response || {})
    } catch (error) {
      console.error('Error fetching prices:', error)
    }
  }

  const fetchOracleStats = async () => {
    try {
      const response = await fcl.query({
        cadence: `
          import FlowOracle from ${ORACLE_ADDRESS}
          
          pub fun main(): {String: UInt64} {
            return {
              "quorumSize": FlowOracle.getQuorumSize(),
              "nodeCount": FlowOracle.getNodeCount()
            }
          }
        `
      })
      setOracleStats(response || {})
    } catch (error) {
      console.error('Error fetching oracle stats:', error)
    }
  }

  return (
    <div className="App">
      <header className="header">
        <div className="container">
          <h1>FlowOracle</h1>
          <p className="subtitle">Decentralized Price Feed for Flow DeFi</p>
          <WalletConnect user={user} />
        </div>
      </header>

      <main className="container">
        <div className="oracle-stats">
          <div className="stat-card">
            <h3>Quorum Size</h3>
            <p className="stat-value">{oracleStats.quorumSize || 0}</p>
          </div>
          <div className="stat-card">
            <h3>Price Nodes</h3>
            <p className="stat-value">{oracleStats.nodeCount || 0}</p>
          </div>
          <div className="stat-card">
            <h3>Active Prices</h3>
            <p className="stat-value">{Object.keys(prices).length}</p>
          </div>
        </div>

        <div className="tabs">
          <button 
            className={`tab ${activeTab === 'prices' ? 'active' : ''}`}
            onClick={() => setActiveTab('prices')}
          >
            Current Prices
          </button>
          <button 
            className={`tab ${activeTab === 'history' ? 'active' : ''}`}
            onClick={() => setActiveTab('history')}
          >
            Price History
          </button>
          {user?.addr && (
            <button 
              className={`tab ${activeTab === 'nodes' ? 'active' : ''}`}
              onClick={() => setActiveTab('nodes')}
            >
              Manage Nodes
            </button>
          )}
        </div>

        <div className="tab-content">
          {activeTab === 'prices' && <PriceViewer prices={prices} />}
          {activeTab === 'history' && <PriceHistory oracleAddress={ORACLE_ADDRESS} />}
          {activeTab === 'nodes' && user?.addr && (
            <NodeManagement user={user} oracleAddress={ORACLE_ADDRESS} onUpdate={fetchOracleStats} />
          )}
        </div>
      </main>

      <footer className="footer">
        <p>FlowOracle  Real-time Price Feed | Testnet Beta</p>
      </footer>
    </div>
  )
}
