import React from 'react'

export default function PriceViewer({ prices }) {
  const priceList = Object.entries(prices).sort()

  return (
    <div className="price-viewer">
      {priceList.length === 0 ? (
        <p className="empty-state">No prices available. Submit prices from oracle nodes.</p>
      ) : (
        <div className="price-grid">
          {priceList.map(([pair, price]) => (
            <div key={pair} className="price-card">
              <h3>{pair}</h3>
              <div className="price-value">${price.toFixed(8)}</div>
              <p className="updated-at">Updated now</p>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}
