import React, { useState } from 'react'
import * as fcl from '@onflow/fcl'

export default function PriceHistory({ oracleAddress }) {
  const [selectedPair, setSelectedPair] = useState('FLOW/USDC')
  const [history, setHistory] = useState([])
  const [loading, setLoading] = useState(false)

  const fetchHistory = async (pair) => {
    setLoading(true)
    try {
      const response = await fcl.query({
        cadence: `
          import FlowOracle from ${oracleAddress}
          
          pub fun main(pair: String): [FlowOracle.PricePoint] {
            return FlowOracle.getPriceHistory(pair: pair, limit: 20)
          }
        `,
        args: (arg, t) => [arg(selectedPair, t.String)]
      })
      setHistory(response || [])
    } catch (error) {
      console.error('Error fetching history:', error)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="price-history">
      <div className="history-controls">
        <input
          type="text"
          placeholder="Enter pair (e.g., FLOW/USDC)"
          value={selectedPair}
          onChange={(e) => setSelectedPair(e.target.value)}
          className="pair-input"
        />
        <button onClick={() => fetchHistory(selectedPair)} className="fetch-btn" disabled={loading}>
          {loading ? 'Loading...' : 'Load History'}
        </button>
      </div>

      {history.length === 0 ? (
        <p className="empty-state">Enter a pair and click Load History</p>
      ) : (
        <div className="history-table">
          <table>
            <thead>
              <tr>
                <th>Price</th>
                <th>Timestamp</th>
              </tr>
            </thead>
            <tbody>
              {history.map((point, idx) => (
                <tr key={idx}>
                  <td>${point.price.toFixed(8)}</td>
                  <td>{new Date(point.timestamp * 1000).toLocaleString()}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}
