import React, { useState } from 'react'
import * as fcl from '@onflow/fcl'

export default function NodeManagement({ user, oracleAddress, onUpdate }) {
  const [nodeAddress, setNodeAddress] = useState('')
  const [weight, setWeight] = useState('1.0')
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState('')

  const addNode = async () => {
    if (!nodeAddress || !weight) {
      setMessage('Please fill in all fields')
      return
    }

    setLoading(true)
    try {
      const txId = await fcl.mutate({
        cadence: `
          import FlowOracle from ${oracleAddress}
          
          transaction(nodeAddress: Address, weight: UFix64) {
            let oracleRef: &FlowOracle
            
            prepare(signer: AuthAccount) {
              self.oracleRef = signer.borrow<&FlowOracle>(from: /storage/FlowOracle)
                ?? panic("No FlowOracle found")
            }
            
            execute {
              self.oracleRef.addPriceNode(address: nodeAddress, weight: weight)
            }
          }
        `,
        args: (arg, t) => [
          arg(nodeAddress, t.Address),
          arg(weight, t.UFix64)
        ],
        payer: fcl.authz,
        proposer: fcl.authz,
        authorizations: [fcl.authz],
        limit: 100
      })

      setMessage(`Transaction submitted: ${txId}`)
      setNodeAddress('')
      setWeight('1.0')
      onUpdate()
    } catch (error) {
      setMessage(`Error: ${error.message}`)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="node-management">
      <h2>Add Price Node</h2>
      <div className="form-group">
        <label>Node Address</label>
        <input
          type="text"
          placeholder="0x..."
          value={nodeAddress}
          onChange={(e) => setNodeAddress(e.target.value)}
          className="form-input"
        />
      </div>

      <div className="form-group">
        <label>Weight</label>
        <input
          type="number"
          placeholder="1.0"
          value={weight}
          onChange={(e) => setWeight(e.target.value)}
          step="0.1"
          className="form-input"
        />
      </div>

      <button onClick={addNode} disabled={loading} className="submit-btn">
        {loading ? 'Adding Node...' : 'Add Node'}
      </button>

      {message && <p className="message">{message}</p>}
    </div>
  )
}
